* {
  font-family: Montserrat, sans-serif;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  margin: 0 auto;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1 {
  font-size: 52px;
  font-weight: 400;
  line-height: 63px;
}

h3 {
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
}

.section__header {
  text-transform: uppercase;
  color: #000;
  font-size: 24px;
  font-weight: 700;
  line-height: 29px;
}

.about--font {
  color: #000;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}

.price--font {
  color: fontPriceCl;
  font-size: 12px;
  font-weight: 600;
  line-height: 15px;
}

.footer__header--font {
  font-family: Inter;
  font-size: 18px;
  font-weight: 700;
  line-height: 22px;
}

.footer--font {
  font-family: Inter;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
}

.contact-us__header--font {
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
}

.contact-us__header--font--font {
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
}

.contact-us__header--font__label-font {
  color: #5c5c5c;
  font-size: 10px;
  font-weight: 500;
  line-height: 12px;
}

.contact-us__header--font__placeholder-font {
  color: #0006;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
}

.newsletter__header--font {
  text-align: center;
  font-size: 19px;
  font-weight: 600;
  line-height: 23px;
}

.newsletter__header--font--font {
  text-align: center;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
}

.btn {
  color: #fff;
  width: 208px;
  height: 44px;
  cursor: pointer;
  background-color: #e86f54;
  border: none;
  border-radius: 38px;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
  transition: background-color .3s;
  display: flex;
}

.btn:focus {
  background-color: #fa6f54;
  outline: none;
  transition: background-color .3s;
}

.btn:hover {
  background-color: #e25b41;
  transition: background-color .3s;
}

.modal-btn {
  width: 240px;
  height: 37px;
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #e86f54;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
  transition: background-color .3s;
}

.modal-btn:focus {
  background-color: #fa6f54;
  outline: none;
  transition: background-color .3s;
}

.modal-btn:hover {
  background-color: #e25b41;
  transition: background-color .3s;
}

@media screen and (min-width: 768px) {
  h1 {
    font-size: 120px;
    line-height: 146px;
  }

  h3 {
    font-size: 32px;
    line-height: 39px;
  }

  .container {
    max-width: 720px;
    margin: 0 auto;
  }

  .about--font {
    font-size: 20px;
    line-height: 24px;
  }

  .price--font, .delivery--font {
    font-size: 24px;
    line-height: 29px;
  }

  .footer__header--font {
    font-size: 20px;
    line-height: 24px;
  }

  .footer--font {
    font-size: 16px;
    line-height: 19px;
  }

  .contact-us__header--font {
    font-size: 32px;
    line-height: 39px;
  }

  .contact-us__header--font--font {
    font-size: 16px;
    line-height: 20px;
  }

  .contact-us__header--font__label-font, .contact-us__header--font__placeholder-font {
    font-size: 14px;
    line-height: 17px;
  }

  .newsletter__header--font {
    font-size: 28px;
    line-height: 34px;
  }

  .newsletter__header--font --font {
    font-size: 16px;
    line-height: 21px;
  }

  .btn {
    width: 280px;
    height: 64px;
    font-size: 28px;
  }

  .modal-btn {
    width: 416px;
    height: 50px;
  }

  .modal-btn--newsletter {
    width: 336px;
  }

  .section__header {
    font-size: 44px;
    line-height: 54px;
  }
}

@media screen and (min-width: 1280px) {
  h1 {
    font-size: 160px;
    line-height: 195px;
  }

  h3 {
    font-size: 36px;
    line-height: 44px;
  }

  .header__item--font {
    font-size: 20px;
    font-weight: 400;
    line-height: 24px;
  }

  .about--font {
    font-size: 22px;
    line-height: 27px;
  }

  .price--font {
    font-size: 28px;
    line-height: 34px;
  }

  .delivery--font {
    font-size: 32px;
    line-height: 39px;
  }
}

