  padding: 0;
  overflow: visible;
}

.menu-modal {
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  background-color: #e86f54;
  transition: transform .25s;
  position: fixed;
  top: 0;
  left: 0;
  transform: translate(-150%);
}

.menu-modal.is-open {
  transform: translateX(0);
}

.menu-modal__button {
  justify-content: flex-end;
  padding-top: 16px;
  padding-right: 16px;
  display: flex;
}

.menu-modal__button-close {
  fill: #fff;
  cursor: pointer;
  background: none;
  border: none;
}

.menu-modal__list {
  height: 100vh;
  flex-direction: column;
  align-items: center;
  gap: 26px;
  margin: auto;
  padding: 84px 0 0;
