* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

label {
  flex-direction: column;
  display: flex;
}

.backdrop {
  z-index: 1000;
  width: 100%;
  height: 100%;
  background: #0003;
  transition: visibility .4s linear, opacity .4s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.is-hidden__ .modal {
  transform: translate(-50%, -50%)scale(0);
}

.modalContact {
  width: 272px;
  height: 357px;
  background: #fff;
  padding: 28px 16px;
  transition: transform .3s linear;
  display: block;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.modalContact__close {
  cursor: pointer;
  background-color: #0000;
  border: none;
  position: absolute;
  top: 16px;
  right: 16px;
}

